<template>
  <div id="main_view">
    <div id="OMap" style="width: 100%;height: 100%;"></div>
    <div id="logo">
      <b-img src="../assets/logo.png" width="350px" height="83px" />
    </div>
    <div id="mymenu" class="rounded shadow">
      <my-menu />
    </div>
    <div id="layer">
      <font-awesome-icon icon="layer-group" size="4x" @click="logout" />
    </div>
    <div id="version">
      <strong>ver. 0.1.1204</strong>
    </div>
  </div>
</template>

<style>
html,
body {
  margin: 0;
  padding: 0;
}

#main_view {
  font-family: "Microsoft JhengHei", "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100vw;
  height: 100vh;
}

#logo {
  position: absolute;
  left: 20px;
  top: 20px;
  max-width: 350px;
  max-height: 100px;
  z-index: 1000;
}

#mymenu {
  position: absolute;
  left: 20px;
  top: 120px;
  width: 30%;
  height: 75%;
  background-color: whitesmoke;
  font-family: Microsoft JhengHei;
  font-weight: bold;
  overflow: overlay;
  opacity: 0.85;
  z-index: 1000;
}

#avatar {
  position: absolute;
  right: 70px;
  top: 100px;
  width: 70px;
  height: 70px;
  z-index: 1000;
}

#layer {
  position: absolute;
  right: 70px;
  top: 180px;
  width: 70px;
  height: 70px;
  z-index: 1000;
}

#version {
  position: absolute;
  right: 20px;
  bottom: 20px;
  width: 100px;
  height: 40px;
  z-index: 1000;
}
</style>
<script>
let messageBox; //

let pMap; //

//------------------------------,,------------------------------
var infotext = "<B></B><br>"; //
var markerPoint = new TGOS.TGPoint(186065.13, 2621248.49); //
var imgUrl = "http://api.tgos.tw/TGOS_API/images/marker2.png"; //
//------------------------------,---------------------------------------

function InitWnd() {
  //--------------------------------------
  let pOMap = document.getElementById("OMap");
  let mapOptiions = {
    navigationControl: false,
    navigationControlOptions: {
      controlPosition: TGOS.TGControlPosition.LEFT_BOTTOM, //
      navigationControlStyle: TGOS.TGNavigationControlStyle.SMALL //
    },
    scaleControl: true
  };

  pMap = new TGOS.TGOnlineMap(pOMap, TGOS.TGCoordSys.EPSG3826, mapOptiions); //,TWD97

  pMap.setZoom(7); //
  pMap.setCenter(markerPoint); //
  //---------------------------------------

  var markerImg = new TGOS.TGImage(
    imgUrl,
    new TGOS.TGSize(38, 33),
    new TGOS.TGPoint(0, 0),
    new TGOS.TGPoint(10, 33)
  ); //

  var pTGMarker = new TGOS.TGMarker(pMap, markerPoint, "", markerImg); //

  //-------------------------------------

  var InfoWindowOptions = {
    maxWidth: 4000, //

    pixelOffset: new TGOS.TGSize(5, -30), //InfoWindow, TGSize, X, Y

    zIndex: 99 //
  };

  messageBox = new TGOS.TGInfoWindow(infotext, markerPoint, InfoWindowOptions); //

  TGOS.TGEvent.addListener(pTGMarker, "mouseover", openInfoWindow); //--

  TGOS.TGEvent.addListener(pTGMarker, "mouseout", closeInfoWindow); //--
}

function openInfoWindow() {
  //
  messageBox.open(pMap);
}

function closeInfoWindow() {
  //
  messageBox.close();
}

import qs from "qs";
import MyMenu from "@/views/MyMenu";
import axios from "axios";
import { TokenRet } from "../api/types";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "mainView",
  components: {
    MyMenu
  },
  mounted() {
    InitWnd();
  },
  methods: {}
};
</script>